import React from "react";
import TidyCalEmbed from "../components/tidyCalEmbed";
import Layout from "./layout";
import { Company } from "../interfaces";
import data from "../data/data.json";
import { graphql } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next/dist";
import { Helmet } from "react-helmet";

const info: Company = data[0];

const AgendaPage = () => {
  const { t } = useTranslation();
  return (
    <>
      <Helmet>
        <title>{t("keywords.titleAgenda")}</title>
        <meta name="description" content={t("keywords.metaAgenda") || ""} />
      </Helmet>
      <Layout data={info}>
        <div className="bg-terciary p-4 ">
          <div className="w-full  flex justify-center pb-4">
            <a className="text-center text-dark" href="/agenda">
              {t("resetAgenda")}
            </a>
          </div>
          <TidyCalEmbed />
        </div>
      </Layout>
    </>
  );
};

export default AgendaPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
